<template>
  <div id="WindowsView">
    <head-bar title="LockStore" helpBtn resourcesBtn></head-bar>
    <banner-card></banner-card>
    <body-card space-title="热门" color-title="TOP 5" :data="popularData" @downLoadClick="downLoadClick" v-if="popularData"></body-card>
    <body-card space-title="新发布" color-title="New" :data="newReleaseData" @downLoadClick="downLoadClick" v-if="newReleaseData"></body-card>
    <support-card v-if="newReleaseData"></support-card>
    <bottom></bottom>
    <van-dialog v-model="downLoadCardSta" title="下载" confirmButtonText="取消" confirmButtonColor="#6CC8AE">
      <div class="downLoad">
        <p class="downLoad-content" v-if="content">{{ content }}</p>
        <span v-for="(item, index) in downLoadData" :key="index" :style="item.lock_type | lockTypeBackgroundColor" @click="downLoad(item.dowmload_src)">
          <p>{{ item.lock_type | lockTypeText }}</p>
        </span>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import headBar from '@/components/headBar.vue'
import bannerCard from '@/components/bannerCard.vue'
import bodyCard from '@/components/bodyCard.vue'
import bottom from '@/components/bottom.vue'
import SupportCard from '@/components/supportCard.vue'
const ip = 'http://150.158.149.195:33666/'
const userApi = `${ip}api/user/`
const adminApi = `${ip}api/admin/`
const otherApi = `${ip}api/other/`

export default {
  name: 'WindowView',
  components: {
    headBar,
    bannerCard,
    bodyCard,
    bottom,
    SupportCard
  },
  data () {
    return {
      popularData: null,
      newReleaseData: null,
      downLoadCardSta: false,
      downLoadData: null,
      lockTypeBackgroundColor: null,
      content: null
    }
  },
  mounted () {
    this.require()
  },
  methods: {
    require () {
      axios.get(otherApi + 'popularLocks').then(res => {
        res = res.data
        if (res.sta) {
          this.popularData = res.data
        } else this.$store.commit('tips', res.msg)
      }).catch(err => {
        this.$store.commit('tips', `错误：${err.message}`)
      })
      axios.get(otherApi + 'newReleaseLocks').then(res => {
        res = res.data
        if (res.sta) {
          this.newReleaseData = res.data
        } else this.$store.commit('tips', res.msg)
      }).catch(err => {
        this.$store.commit('tips', `错误：${err.message}`)
      })
    },
    downLoadClick (data) {
      const lockId = data.split('|')[0]
      const content = data.split('|')[1]
      axios.get(otherApi + 'lockFiles', {
        params: {
          id: lockId
        }
      }).then(res => {
        const resStatus = res.status == 200
        res = res.data
        if (res.sta) {
          this.content = content == 'null' || content == '' ? '' : `" ${content} "`
          this.downLoadCardSta = true
          this.downLoadData = res.data
        } else this.$store.commit('tips', res.msg)
        resStatus && this.$store.state.downLoadSta ? this.$store.commit('loading', false) : null
        this.$store.commit('changeDownLoadSta', false)
      }).catch(err => {
        this.$store.commit('tips', `错误：${err.message}`)
      })
    },
    downLoad (src) {
      window.location.href = src
    }
  },
  filters: {
    lockTypeBackgroundColor (type) {
      const black = 'background:#000000;color:#ffffff;'
      const white = 'border: 2px solid #000000;box-sizing: border-box;color:#000000;'
      const color = 'background:linear-gradient(45deg, #64bfe9, #74d178);color:#ffffff;'
      return type === 0 ? black : type === 1 ? white : type === 2 ? color : null
    },
    lockTypeText (type) {
      return type === 0 ? '黑色' : type === 1 ? '白色' : type === 2 ? '彩色' : null
    }
  }
}
</script>

<style lang="scss" scoped>
#WindowsView{
  width: 380px;
  display: flex;
  align-items: center;
  flex-direction: column;
  .downLoad{
    width: 80%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    .downLoad-content{
      margin-top: 15px;
      color: #787878;
    }
    span{
      width: 100%;
      height: 45px;
      border-radius: 20px;
      margin: 6px 0px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bolder;
      transition: all .3s;
    }
    span:first-of-type{
      margin-top: 20px;
    }
    span:last-of-type{
      margin-bottom: 20px;
    }
  }
}
</style>